.custom-form-content-main {
  flex-direction: column;
  border-left: 1px solid #d9d9d9;
  border-right: 1px solid #d9d9d9;
}
.custom-form-operation-content {
  overflow: hidden;
  flex: 1 1 0;
  background: #fff;
  padding: 8px 16px;
}
.custom-form-json-show {
  overflow: overlay;
  height: 100%;
}
.custom-form-json-show .custom-form-json-show-main {
  pointer-events: none;
}
.custom-form-content-header {
  border-bottom: 1px solid #d9d9d9;
  padding: 4px;
}
